import { CATEGORY } from "../constant";
import axios from "axios";
import { API_SERVER } from '../../config'
import { message } from "antd";

const API = axios.create({
    baseURL: API_SERVER + '/api'
});


export const saveCategory = data => ({ type: CATEGORY, data })
export const getCategory = token => {
    // console.log('action category')
    return async (dispatch) => {
        try {
            const res = await API.post('/getcategory', {}, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            })
            // console.log(res)
            const arr = res.data.data
            //split
            let temp = []
            arr.forEach(element => {
                const splitArr = element.keypath.split('/')
                const length = splitArr.length
                if (!temp[length]) temp[length] = []
                temp[length].push(splitArr)
            });
            //
            let data = {}
            for (let i = 1; i < temp.length; i++) {
                const nodeArr = temp[i];
                nodeArr.forEach(node => {
                    let pointer = data
                    for (let j = 0; j < node.length - 1; j++) {
                        pointer = pointer[node[j]] //
                    }
                    pointer[node[node.length - 1]] = {}
                });
            }
            // console.log(data)
            dispatch(saveCategory(data))
            return true
        } catch (error) {
            // console.log(error)
            if (!error.response) {
                message.error('')
                return false
            }
            const data = error.response.data
            const errorMessage = data ? data.message : error.message
            message.error(errorMessage)
            return false
        }

    }
}


