import React, { useEffect } from 'react'
import { Layout } from 'antd'
import MyBreadcrumb from '../../components/MyBreadcrumb'
import MyContent from '../../containers/MyContent'
import { useLocation } from 'react-router-dom'
import { connect } from 'react-redux'
import { getBookmark,saveBookmark } from '../../redux/actions/bookmark'
import MyUser from '../../containers/MyUser'
import MyHeader from '../../components/MyHeader'
import './index.css'
import AddBookmark from '../../components/OperateBookmark'
import { saveKeypath } from '../../redux/actions/keypath'
import OperateBookmark from '../../components/OperateBookmark'
function BookmarkLayout(props) {
  const { pathname } = useLocation()
  const parseUrl = pathname.match(/^\/bookmark\/(.*)/)
  const keyPath = parseUrl ? decodeURI(parseUrl[1]) : '' // bookmarkbookmark/
  props.saveKeypath(keyPath)

  useEffect(()=>{
    // console.log(props.user)
    if(props.user.username){
      props.getBookmark(keyPath,props.user.token)
    }else{
      props.saveBookmark()//
    }
  })
  // console.log('render BMlayout')
  return (
    <Layout
      style={{
        padding: '0 24px 24px',
      }}
    >
      <div className='header'>
        <MyBreadcrumb keyPath={keyPath} />      
        <MyUser></MyUser>
        
      </div>
      <OperateBookmark keyPath={keyPath}></OperateBookmark>
      
      
      <MyContent keyPath={keyPath} />

    </Layout>

  )
}

export default connect(
  state => ({
    user:state.user
  }),
  dispatch => ({
    getBookmark: (path,token) => dispatch(getBookmark(path,token)),
    saveBookmark: () => dispatch(saveBookmark()),
    saveKeypath:keyPath =>dispatch(saveKeypath(keyPath))
    
  })
)(BookmarkLayout)