import axios from 'axios';
import { API_SERVER } from '../../config'
import { message } from "antd";

const API = axios.create({
    baseURL: API_SERVER + '/api'
});

export const addBookmark = (token,data) => {
    return async (dispatch)=>{
        try {
            const res = await API.post('/addbookmark', data, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            })
            // console.log(res)
            return true
        } catch (error) {
            // console.log(error)
            if (!error.response) {
                message.error('')
                return false
            }
            const data = error.response.data
            const errorMessage = data ? data.message : error.message
            message.error(errorMessage)
            return false
        }
        
    }
}

