import { BOOKMARK } from "../constant";


import md5 from "crypto-js/md5";
import imgData from './img.json'
import axios from "axios";
import { API_SERVER } from '../../config'
import { message } from "antd";

const API = axios.create({
    baseURL: API_SERVER + '/api'
});


const defaultImg = imgData.img
export const saveBookmark = data => ({ type: BOOKMARK, data })
export const getBookmark = (keypath,token) => { //
    // console.log('action bm')
    return async (dispatch) => {
        //store
        try {
            const res = await API.post('/getbookmark', {
                keypath
            }, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            })
            // console.log('store bm')

            const bmData = res.data.data
            const bookmarkData = bmData.map(obj => {
                const { updatedAt } = obj
                let url = obj.url ? obj.url : ''
                const intro = obj.intro ? obj.intro : ''
                const img = obj.img ? obj.img : defaultImg
                let title = obj.title ? obj.title : intro
                let res = title.match(/- ([^-.]*$)/)
                if (res) title = res[1]
                //URL 
                // console.log(url)
                let thisURL, domain
                
                if (url !== '') {
                    thisURL = url.search('http') === -1 ? new URL('http://' + url) : new URL(url)
                    domain = thisURL ? thisURL.hostname : ''

                } else {
                    thisURL = ''
                    domain = ''
                }
                url = url.search('http') === -1 ? 'http://' + url : url

                //console.log(new URL(url))   
                // res = new URL(url).hostname.match(/[\w-]+\.\w+$/)
                // const domain = res ? res[0] : ''
                //const domain = thisURL?thisURL.hostname:''
                //const img = thisURL.origin ? thisURL.origin +'/favicon.ico' : '' //
                const note = obj.note ? obj.note : ''
                const id = obj._id ? obj._id : md5(JSON.stringify(obj)).toString()


                return {
                    intro, url, domain, note, title, id, img, updatedAt
                }
            })

            dispatch(saveBookmark(bookmarkData))

        } catch (error) {
            message.error(error.message)
        }
        
    }
}
