import { CATEGORY } from "../constant";

const initState={
    '1': {
        '': {
            
        },
        '': {
            '1': {},
            '2': {}
        }
    },
    '2': {
        
      
    },
} //
export default function categoryReducer(preState=initState,action){
    const { type, data } = action
    // console.log('reducer category', action)
    switch (type) {
        case CATEGORY:
            if(!data)return initState
            return data
        default:
            return preState
    }
}