import React from 'react'
import './index.css'
import { DeleteOutlined, LinkOutlined } from '@ant-design/icons'
import {  message } from 'antd'
import { connect } from 'react-redux'
import { deleteBookmark } from '../../redux/actions/api'
import { getBookmark } from '../../redux/actions/bookmark'


function Card(props) {
    const {id,title,intro,url,note,domain,img} = props
    const user = props.user
   
    async function deleteBookmark(){
        let res
        if (user.username) {
            res = await props.deleteBookmark(user.token,
                {id})
        } else {
            res = false
            message.error('')
        }
        if (res) {
            await props.getBookmark(props.keyPath, user.token)     
        } else {            
            // console.log('error:deleteBookmark')
        }


    }
    return (
        <section className="card">
            <div className="card-header">
                <div className="title-left">
                    <div className="img-box">
                        <img className="img-icon" width="40" height="40" alt="ICO" src={img} ></img>
                    </div>
                    <div className="title">{title}</div>
                </div>

            </div>
            <div className="card-content">
                <div className="desc">{intro}</div>
        
                <label className='card-input ant-input ant-input-borderless'>{note}</label>
            </div>

            <div className="card-footer">
                <a href={url} target="_blank" rel="noopener noreferrer"><LinkOutlined className='icon' href='baidu.com'></LinkOutlined></a>
                
                <span className='card-url'>{domain}</span>
                <DeleteOutlined className='icon' onClick={ deleteBookmark} />
            </div>

            
        </section>

    )
}
export default connect(state=>({
    user:state.user
}),{
    deleteBookmark,
    getBookmark
    

})(Card)