import React, { useState } from 'react'
import './index.css'
import { UserOutlined, SearchOutlined, AuditOutlined, LoginOutlined, LogoutOutlined, MailOutlined, KeyOutlined, SolutionOutlined } from '@ant-design/icons';
import { Avatar, Button, Image, Space, Modal, Input, message, } from 'antd';
import { connect } from 'react-redux'
import { postUser, cleanUser } from '../../redux/actions/user';
import AddBookmark from '../../components/OperateBookmark';
import { getBookmark } from '../../redux/actions/bookmark';
import { useNavigate } from 'react-router-dom';


function MyUser(props) {
    const navigate = useNavigate()
    const [isLogin, setIsLogin] = useState(true)
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [username, setUsername] = useState()
    const [password, setPassword] = useState()
    const user = props.userData

    const showModal = (login) => {
        return () => {
            if (login) setIsLogin(true)
            else setIsLogin(false)
            setIsModalVisible(true);
        }
    };

    const handleOk = async () => {
        setConfirmLoading(true);

        const res = await props.postUser(isLogin, username, password)
        if (res) {
            setConfirmLoading(false);
            setIsModalVisible(false);
            setUsername()
            setPassword()
            navigate('/')
        } else {
            setConfirmLoading(false);
        }

    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const exitUser = () => {
        props.cleanUser()
        message.warning('')
        navigate('/')
    }

    return (
        <div className='user-div'>
            <Space>

                {!user.username &&
                    <>
                        <Button type="text" icon={<LoginOutlined />} onClick={showModal(true)}></Button>
                        <Button type="text" icon={<AuditOutlined />} onClick={showModal(false)}></Button>
                    </>}
                {user.username &&
                    <Button type="text" icon={<LogoutOutlined />} onClick={exitUser}></Button>
                }

                <Avatar size='large' >{user.username ? user.username.slice(0, 2) : ''}</Avatar>
            </Space>
            <Modal title={isLogin ? "" : ""}
                visible={isModalVisible}
                onOk={handleOk} onCancel={handleCancel}
                confirmLoading={confirmLoading}
                okText={isLogin ? "" : ""}
                cancelText="">

                <Space direction='vertical' size='middle' style={{
                    display: 'flex',
                    flexDirection: 'column'
                }}>

                    <Input size="large" placeholder="" prefix={<SolutionOutlined />}
                        onChange={(event) => setUsername(event.target.value)}
                        value={username}
                    />
                    <Input.Password size="large" placeholder="" prefix={<KeyOutlined />}
                        onChange={(event) => setPassword(event.target.value)}
                        value={password}
                    />
                </Space>


            </Modal>

        </div>
    )
}
export default connect(
    state => ({
        userData: state.user
    }),
    {
        postUser, cleanUser, getBookmark
    }
)(MyUser)