import React, { useEffect, } from 'react'
import MySider from '../../containers/MySider';
import { getCategory,saveCategory } from '../../redux/actions/MySider';
import { connect } from 'react-redux'
function SiderLayout(props) {
    // console.log('render SiderLayout')
    useEffect(() => {
        if (props.user.username) {
            props.getCategory(props.user.token)
        } else {
            props.saveCategory() 
            //
        }
    })

    return (
        <MySider />
    )
}
export default connect(
    state => ({
        user: state.user
    }),
    {
        saveCategory,
        getCategory
    }
)(SiderLayout)