import React from 'react'
import { Breadcrumb } from 'antd';
import { Link } from 'react-router-dom';

export default function MyBreadcrumb(props) {
    let { keyPath } = props
    if(keyPath===null ||keyPath==='')keyPath=''
    const keyPathArr = keyPath.split('/')

    return (
        <Breadcrumb
            separator=">"
            style={{
                margin: '16px 0',
            }}
        >{
                keyPathArr.map((path, index) => {
                    const keyPath = keyPathArr.slice(0, index + 1).join('/')
                   
                    return <Breadcrumb.Item key={keyPath}>
                        <Link to={`/bookmark/${keyPath}`} >
                            {path}
                        </Link>
                    </Breadcrumb.Item>
                })
            }

        </Breadcrumb>
    )
}
