import React, { useState } from 'react'
import { Button, Space, Modal, Input, Avatar, Image, Select, message } from 'antd'
import { PlusOutlined, FolderAddOutlined, SyncOutlined, } from '@ant-design/icons'
import './index.css'
import { connect } from 'react-redux'
import imgData from '../../redux/actions/img.json'
import { addBookmark } from '../../redux/actions/api'
import { getBookmark } from '../../redux/actions/bookmark'
import { getCategory } from '../../redux/actions/MySider'
import { addCategory } from '../../redux/actions/api'

const defaultImg = imgData.img

const { TextArea } = Input
const { Option } = Select;

function OperateBookmark(props) {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [isCategoryVisible, setCategoryVisible] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [CGconfirmLoading, setCGConfirmLoading] = useState(false);
    const [data, setData] = useState({})

    //
    const [title, setTitle] = useState()
    const [intro, setIntro] = useState()
    const [note, setNote] = useState()
    const [url, setUrl] = useState()
    const [imgSrc, setImgSrc] = useState(defaultImg)
    const [img, setImg] = useState() //

    //
    const [path, setPath] = useState()


    const [option, setOption] = useState('default')
    const user = props.user


    const handleBM = async () => {

        // console.log(JSON.stringify({ title, intro, note, url, img }))
        setConfirmLoading(true);

        let res
        if (user.username) {
            res = await props.addBookmark(user.token,
                { title, intro, note, url, img, keypath: props.keyPath })
        } else {
            res = false
            message.error('')
        }
        if (res) {
            await props.getBookmark(props.keyPath, user.token)
            setConfirmLoading(false);
            setIsModalVisible(false);
        } else {
            setConfirmLoading(false);
        }
    };
    const handleCG = async () => {
        setCGConfirmLoading(true);
        let res
        let keypath = props.keyPath
        if (!path || path.trim() === '' || path.search('/') !== -1) {
            res = false
            message.error('/')
        } else {
            if (user.username) {
                if (keypath === '' || keypath==='') keypath = path
                else keypath += '/' + path
                res = await props.addCategory(user.token,
                    { keypath })
            } else {
                res = false
                message.error('')
            }
        }
        //const res = await props.postUser(isLogin, username, password)
        if (res) {
            await props.getCategory(user.token)
            setCGConfirmLoading(false);
            setCategoryVisible(false);
        } else {
            setCGConfirmLoading(false);
        }
    };
    const select = (value) => {
        switch (value) {
            case 'default':
                setOption('default')
                setImgSrc(defaultImg)
                setImg()
                break;
            case 'ico':
                if (url.trim()!=='') {
                    const srcUrl = url.search('http') === -1 ? 'http://' + url : url //src
                    setOption('ico')
                    const icoUrl = new URL(srcUrl).origin + '/favicon.ico'
                    setImgSrc(icoUrl)
                    setImg(icoUrl)
                }
                else message.error('url')
                break

            default:

                break;
        }
    }
    const srcError = () => {
        setOption('default')
        setImgSrc(defaultImg)
        setImg()
        message.error('url')
    }

    const refresh = async ()=>{
        if (user.username) {
            await props.getBookmark(props.keyPath, user.token)
            await props.getCategory(user.token)
            message.success('')
        } else {
            message.error('')
        }
    }

    return (
        <div className='operation-button'>
            <Space>
                <Button shape="circle" icon={<PlusOutlined />} onClick={() => setIsModalVisible(true)} size='large' />
                <Button shape="circle" icon={<FolderAddOutlined onClick={() => setCategoryVisible(true)} />} size='large' />
                <Button shape="circle" icon={<SyncOutlined />} onClick={refresh} size='large' />
            </Space>

            <Modal title=""
                visible={isModalVisible}
                onOk={handleBM} onCancel={() => setIsModalVisible(false)}
                width={600} okText='' cancelText=''
                confirmLoading={confirmLoading}>

                <div className='add-bookmark'>

                    <Space direction='vertical' size='middle' >
                        <label></label><Input size='medium' onChange={(e) => setTitle(e.target.value)} value={title}></Input>
                        <TextArea placeholder='' rows={2} onChange={(e) => setIntro(e.target.value)} value={intro} />
                        <Input size='large' placeholder='' addonBefore={<label></label>} onChange={(e) => setNote(e.target.value)} value={note}></Input>
                        <Input size='large' placeholder='http://' addonBefore={<label>URL</label>} onChange={(e) => setUrl(e.target.value)} value={url}></Input>

                    </Space>
                    <div className='logo-pick'>
                        <Space direction='vertical'>
                            <label></label>
                            <Select defaultValue="default" className="select-before" style={{ 'width': '100px' }}
                                onSelect={select} value={option}
                            >
                                <Option value="default"></Option>
                                <Option value="ico">ico</Option>

                            </Select>

                            <Avatar src={imgSrc} style={{
                                'width': '48px',
                                'height': '48px',
                                'backgroundColor': 'white'
                            }} onError={srcError} ></Avatar>
                        </Space>
                    </div>
                </div>

            </Modal>


            <Modal title=""
                visible={isCategoryVisible}
                onOk={handleCG} onCancel={() => setCategoryVisible(false)}
                width={600} okText='' cancelText=''
                confirmLoading={CGconfirmLoading}>
                <Space direction='vertical'>
                    <label></label><Input addonBefore={<label>{
                        props.keyPath === '' ? '/' : props.keyPath+'/'
                        } </label>} style={{ 'width': '400px' }}
                        onChange={(e) => setPath(e.target.value)} value={path}></Input>
                </Space>
            </Modal>
        </div>

    )
}
export default connect(
    state => ({
        user: state.user,

    }), {
    addBookmark,
    getBookmark,
    getCategory,
    addCategory


}
)(OperateBookmark)