import React, { useEffect } from 'react'
import { Button, Layout } from 'antd'
import Card from '../../components/Card'
import './index.css'
import { getBookmark } from '../../redux/actions/bookmark'
import { connect } from 'react-redux'
import ErrorBoundary from 'antd/lib/alert/ErrorBoundary'

const { Content } = Layout

function BookmarkContent(props) {
    //click
    // console.log('render BookmarkContent',props)
    const {bookmark} = props
    
    
    return (
        <Content
            className="site-layout-background card-main"
            style={{               
                margin: 0,
                minHeight: 280,
            }}
        >
            <ErrorBoundary>
            {bookmark.map(obj => {
               return <Card key={obj.id} {...obj} keyPath={props.keyPath}></Card>
            })}
            </ErrorBoundary>
        </Content>
    )
}
//reduxstate
export default connect(
    state => ({
        bookmark:state.bookmark
    }),
    {

    }
)(BookmarkContent)

