import React from 'react'
import { Layout, Menu } from 'antd';
import { FolderOutlined, BookOutlined, BookFilled } from '@ant-design/icons'
import './index.css'
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import AddBookmark from '../../components/OperateBookmark';

const { Sider } = Layout

function MySider(props) {
    // console.log('render MySider')
    const { categoryData } = props

    //antd menu
    function searchObj(obj, path) {
        
        const objKeys = Object.keys(obj)
        return objKeys.map(key => {
            path.push(key)
            const keyPath = path.join('/')
            const menuObj = {
                key: keyPath,
                label: <Link className='link' to={`/bookmark/${keyPath}`} >{key}</Link>,
                icon: <FolderOutlined />
            }
            if (Object.keys(obj[key]).length !== 0) {
                menuObj.children = searchObj(obj[key], path)
            }
            path.pop()
            return menuObj
        })
    }
    //item
    const itemFirst = {
        key: '',
        label: <Link className='link' to='bookmark/' state={{ key: '' }}></Link>,
        icon: <BookFilled />
    }

    const items = [itemFirst, ...searchObj(categoryData, [])]

    return (
        <Sider width={270} className="site-layout-background">
            
            <Menu
                mode="inline"
                defaultSelectedKeys={[]} //redux
                defaultOpenKeys={[]}
                style={{
                    height: '100%',
                    borderRight: 0,
                }}
                items={items}
                subMenuCloseDelay='0'
            //onOpenChange={e => {console.log(e)}  }
            //
            />
            
        </Sider>
    )
}
export default connect(
    state =>({
       categoryData:state.category
    }),
    dispatch =>({
        
    })
)(MySider)