import './App.css'
import React from 'react';
import { Layout } from 'antd';
import MyHeader from './components/MyHeader';
import MySider from './pages/SiderLayout';
import BookmarkLayout from './pages/BookmarkLayout';
import { Navigate, Route, Routes } from 'react-router-dom';
import AddBookmark from './components/OperateBookmark';
import { message } from 'antd';

export default function App() {
  message.config({
    top: 45,
  });

  return (
    <>
   
    
    <Layout
      style={{
        minHeight: '100vh',
      }}>
      
      <Layout>
       
        <MySider />
        <Routes>
          <Route path='/bookmark/*' element={<BookmarkLayout />}></Route>
          <Route path='/' element={<Navigate to='/bookmark'></Navigate>}></Route>
        </Routes>


      </Layout>
    </Layout>
    </>

  )
}
