import { BOOKMARK } from "../constant";

const initState = [{
    id: 0,
    title: 'MarksBox ',//intro
    intro: 'MarksBox ',
    url: 'https://marksbox.github.io',//url
    domain: 'marksbox.github.io',//url
    note: '# # #TODO #v1.0',//
    updatedAt: '',
    img:'/favicon.ico'
}, ] 
export default function bookmarkReducer(preState=initState,action){
    const{type, data} = action
    // console.log('reducer bookmark', action)
    switch(type){
        case BOOKMARK:
            if(!data)return initState //actiondata
            return data
        default:
            return preState
    }
}