import { SAVE_USER, CLEAN_USER } from "../constant";
import axios from "axios";
import { API_SERVER } from '../../config'
import { message } from "antd";

const API = axios.create({
    baseURL: API_SERVER + '/api'
});


export const cleanUser = () =>({ type:CLEAN_USER}) //data
export const saveUser = (data) => ({ type: SAVE_USER, data })
export const postUser = (isLogin, username, password) => {
    // console.log('action postUser')
    return async (dispatch) => {
        const path = isLogin ? '/login' :'/register'
        try {
            const res = await API.post(path, { username, password })
            // console.log(res)
            const data = res.data
            if(data){
                const {username,token} = data
                dispatch(saveUser({username,token}))
                message.success(data.message)
            }else{
                message.info('')
                return false
            }

            return true
        } catch (error) {     
            // console.log(error)
            if(!error.response){
                message.error('')
                return false
            }
            const data = error.response.data
            const errorMessage = data?data.message:error.message
            message.error(errorMessage)
            return false
        }
        
        


        //dispatch(saveUser({username:'binge',password:'123456'}))
        //return true
    }
}